* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  /* cursor: none; */
}

body {
  /* background-color: black; */
}

.loader-container {
  position: fixed;
  background-color: #050402;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10%;
  opacity: 1;
  transition: opacity 1000ms;
}

.loader-container.loaded {
  pointer-events: none;
  user-select: none;
  opacity: 0;
}

.loader-container h2 {
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 32px;
  font-weight: 800;
  padding: 5px 0;
}

.loader-container h4 {
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: 500;
  padding: 5px 0 20px 0;
}

.loading-bar {
  height: 18px;
  width: 80%;
  max-width: 600px;
  border: solid 2px white;
  position: relative;
}

.bar {
  background: #093028; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #237a57,
    #093028
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #237a57,
    #093028
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  width: 100%;
  height: 100%;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 400ms;
}

.canvas-container {
  min-height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
}

main {
  /* background-color: antiquewhite; */
  width: 100%;
  min-height: 100vh;
  position: relative;
  padding: 0 10%;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: black;
  transition: background-color 500ms;
}
.menu-mobile {
  display: none;
}

main.GM {
  background-color: #050402;
}

main.KM {
  background-color: #0b032d;
}

main.MK {
  background-color: #031a6b;
}

main.MO {
  background-color: #d71093;
}

main.PS {
  background-color: #0092b6;
}

main.VC {
  background-color: #06480a;
}

section {
  width: 100%;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1400px;
  position: relative;
}

nav {
  width: 100%;
  min-height: 60px;
  max-width: 1400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: fit-content;
  padding: 2% 0;
}

.menu-icon {
  fill: white;
  display: none;
  position: relative;
  z-index: 10;
}

nav h3 {
  color: white;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  transition: color 400ms;
}

nav ul {
  display: inline-flex;
  position: relative;
  z-index: 3;
}

nav ul li {
  text-decoration: none;
  list-style: none;
  margin: 5px 25px;
}

.nav-icon {
  fill: white;
  cursor: pointer;
  transform: scale(1) translateY(0%);
  transition: transform cubic-bezier(0.175, 0.885, 0.32, 1.275) 400ms,
    fill ease 450ms;
}

.nav-icon:hover {
  transform: scale(1.15) translateY(-10%);
}

.nav-icon:hover.GM {
  fill: #2f9c95;
}

.nav-icon:hover.KM {
  fill: #fdad85;
}

.nav-icon:hover.MK {
  fill: #05b2dc;
}

.nav-icon:hover.MO {
  fill: #360068;
}

.nav-icon:hover.PS {
  fill: #3a0ca3;
}

.nav-icon:hover.VC {
  fill: #d8ff00;
}

.container {
  width: 100%;
  max-width: 1400px;
  min-height: 80vh;
  position: relative;
}

.absolute-container {
  width: 100%;
  height: 380px;
  margin-bottom: 30px;
}

.content-model {
  margin-right: auto;
  padding-top: 5%;
  position: absolute;
  z-index: 3;
  pointer-events: none;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 300ms;
}

.content-model.active {
  opacity: 1;
  transition: opacity 300ms 400ms;
  pointer-events: all;
}

.content-model h2 {
  color: white;
  font-size: 60px;
  font-family: "Poppins", sans-serif;
  font-weight: 800;
}

.content-model h4 {
  color: white;
  margin-top: 8%;
  max-width: 470px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  line-height: 160%;
}

.container-bottom {
  width: 100%;
  height: 300px;
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.container-bottom h4 {
  color: white;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  position: relative;
  z-index: 3;
}

.coding-text {
  color: white;
  transition: color 400ms;
  text-decoration: underline;
}

.coding-text:hover {
  color: #2f9c95;
  text-decoration: none;
}

.coding-text.GM:hover {
  color: #2f9c95;
}

.coding-text.KM:hover {
  color: #fdad85;
}

.coding-text.MK:hover {
  color: #05b2dc;
}

.coding-text.MO:hover {
  color: #360068;
}

.coding-text.PS:hover {
  color: #3a0ca3;
}

.coding-text.VC:hover {
  color: #d8ff00;
}

.link {
  text-decoration: none;
}

.link.GM {
  color: #2f9c95;
  cursor: pointer;
}

.link.KM {
  color: #fdad85;
  cursor: pointer;
}

.link.MK {
  color: #05b2dc;
  cursor: pointer;
}

.link.MO {
  color: #360068;
  cursor: pointer;
}

.link.PS {
  color: #3a0ca3;
  cursor: pointer;
}

.link.VC {
  color: #d8ff00;
  cursor: pointer;
}

.slide-left {
  background-color: white;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  position: absolute;
  pointer-events: all;
  z-index: 5;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
  transition: background-color 400ms;
  display: none;
  align-items: center;
  justify-content: center;
  transform: translateX(-40%);
}

.icon-slider {
  fill: white;
  transition: fill 400ms;
}

.icon-slider.GM {
  fill: #050402;
}

.icon-slider.KM {
  fill: #0b032d;
}

.icon-slider.MK {
  fill: #031a6b;
}

.icon-slider.MO {
  fill: #d71093;
}

.icon-slider.PS {
  fill: #0092b6;
}

.icon-slider.VC {
  fill: #06480a;
}

.slide-right {
  background-color: white;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  position: absolute;
  pointer-events: all;
  z-index: 5;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
  display: none;
  align-items: center;
  justify-content: center;
  transform: translateX(40%);
}

.slider-container {
  width: 70%;
  height: 200px;
  position: relative;
}

.info-icon {
  fill: white;
  cursor: pointer;
  transform: scale(1);
  transition: transform cubic-bezier(0.175, 0.885, 0.32, 1.275) 400ms,
    fill 550ms;
}

.info-icon:hover {
  transform: scale(1.15);
  fill: red;
}

.swiper-container {
  height: 100%;
}

.swiper-slide {
  width: 33.3% !important;
  height: 100% !important;
  padding: 10px 10px;
}

.img-slide {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
  transform: scale(1);
}

@media only screen and (max-width: 1600px) {
  .slider-container {
    width: 70%;
    height: 180px;
  }
}

@media only screen and (max-width: 1026px) {
  nav {
    padding: 4% 0%;
  }

  nav ul {
    display: none;
  }

  .menu-mobile {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 7;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
    pointer-events: none;
    background-color: #050402;
    clip-path: circle(50px at 100% -10%);
    -webkit-clip-path: circle(50px at 100% -10%);
    transition: clip-path 1400ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: -webkit-clip-path 1400ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    will-change: clip-path;
    will-change: -webkit-clip-path;
  }

  .menu-mobile.GM {
    background-color: #050402;
  }

  .menu-mobile.KM {
    background-color: #0b032d;
  }

  .menu-mobile.MK {
    background-color: #031a6b;
  }

  .menu-mobile.MO {
    background-color: #d71093;
  }

  .menu-mobile.PS {
    background-color: #0092b6;
  }

  .menu-mobile.VC {
    background-color: #06480a;
  }

  .menu-mobile.active {
    pointer-events: all;
    display: flex;
    opacity: 1;
    clip-path: circle(150% at 100% -5%);
    -webkit-clip-path: circle(150% at 100% -5%);
  }

  .menu-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 30px 10px;
  }

  .menu-item .icon {
    fill: white;
    width: 24px;
    height: 24px;
  }

  .menu-item a {
    margin-left: 6px;
    color: white;
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 22px;
    text-decoration: none;
  }

  .menu-icon {
    display: block;
  }

  .container-bottom h4 {
    display: none;
  }

  .slider-container {
    width: 100%;
    height: 150px;
  }
}

@media only screen and (max-width: 767px) {
  main {
    padding: 0 8%;
  }

  nav ul {
    display: none;
  }

  .menu-item .icon {
    fill: white;
    width: 18px;
    height: 18px;
  }

  .menu-item a {
    margin-left: 6px;
    color: white;
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 18px;
    text-decoration: none;
  }
  .absolute-container {
    width: 100%;
    height: 330px;
    margin-bottom: 20px;
  }

  .content-model {
    padding-top: 8%;
  }

  .content-model h2 {
    color: white;
    font-size: 30px;
  }
  .content-model h4 {
    max-width: 90%;
    font-size: 15px;
    font-weight: 600;
  }

  .slide-right,
  .slide-left {
    display: flex;
  }

  .slider-container {
    width: 100%;
    height: 200px;
  }

  .link {
    text-decoration: underline;
  }

  .swiper-slide {
    width: 90% !important;
    height: 100% !important;
    padding: 10px 10px;
  }
}
